@import '../../../styles/variables';

/* üîπ HERO */
.hero {
  color: var(--text-color); /* ‚úÖ Dynamiczny kolor tekstu */
  text-align: center;
  padding: 6rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 85vh;
  position: relative;
  background: var(--hero-bg); /* ‚úÖ Dynamiczne t≈Ço */
  transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
}

/* üîπ KONTENER */
.container {
  max-width: 1200px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  gap: 3rem;
}

/* üîπ TEKST W HERO */
.heroContent {
  max-width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  animation: fadeIn 1s ease-in-out;
}

/* üîπ NAG≈Å√ìWEK */
.hero h1 {
  font-family: $heading-font;
  font-size: 3rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 1rem;
  letter-spacing: 1.5px;
  word-spacing: 5px; /* ‚úÖ Lekko rozsuniƒôte s≈Çowa */
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-text));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: flex;
  flex-wrap: wrap; /* ‚úÖ Pozwala na dynamiczne rozmieszczenie s≈Ç√≥w */
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 90%;
  line-height: 1.3; /* ‚úÖ Mniejsze odstƒôpy pionowe */
  padding: 0 10px;
}

/* üîπ ANIMOWANY TEKST */
.subtitle {
  font-size: 1.5rem;
  color: var(--secondary-text);
  margin-bottom: 2rem;
  font-family: $heading-font;
  min-height: 40px;
  display: block;
  overflow: hidden;
  max-width: 90vw;
  word-break: break-word; /* ‚úÖ Zawijanie d≈Çugich wyraz√≥w */
  text-align: left;
}

/* üîπ KURSOR */
.cursor {
  display: inline-block;
  font-size: 1.5rem;
  margin-left: 5px;
  animation: blink 1s infinite;
}

@keyframes blink {
  50% { opacity: 0; }
}

/* üîπ PRZYCISKI CTA */
.buttons {
  display: flex;
  gap: 0.8rem;
}

.button {
  display: inline-block;
  background: var(--button-bg);
  color: var(--button-text);
  padding: 0.8rem 1.8rem;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.3s ease-in-out, transform 0.2s;
  font-size: 1.1rem;
  min-width: 150px;
  text-align: center;
}

.button:hover {
  transform: scale(1.05);
}

/* üîπ DRUGI PRZYCISK */
.button.secondary {
  background: transparent;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.button.secondary:hover {
  color: var(--primary-color);
}

/* üîπ ZDJƒòCIE */
.heroImage img {
  max-width: 400px;
  border-radius: 15px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  animation: float 6s ease-in-out infinite;
}

/* üîπ LEKKI EFEKT P≈ÅYNIƒòCIA */
@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0px); }
}

/* üîπ ANIMACJA PRZEWIJANIA */
.scrollIndicator {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%); /* ‚úÖ Strza≈Çka idealnie wy≈õrodkowana */
  font-size: 2rem;
  animation: bounce 2s infinite;
  color: var(--secondary-text);
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

/* üîπ FADE-IN */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* üîπ LINIA NA DOLE */
.hero::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: var(--border-color);
  position: absolute;
  bottom: 0;
  left: 0;
}

/* üîπ RESPONSYWNO≈öƒÜ */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    text-align: center;
  }

  .heroContent {
    max-width: 100%;
    align-items: center;
  }

  .heroImage img {
    max-width: 280px;
    margin-top: 2rem;
  }

  .hero h1 {
    font-size: 2.2rem; /* ‚úÖ Mniejszy, ale czytelny */
    letter-spacing: 1px;
    word-spacing: 2px;
    text-align: center; /* ‚úÖ Wy≈õrodkowanie tekstu */
    display: block; /* ‚úÖ Zapobiega zbiciu w wƒÖskƒÖ kolumnƒô */
    max-width: 90vw; /* ‚úÖ Ograniczenie szeroko≈õci dla lepszego dopasowania */
    padding: 0 10px;
    line-height: 1.3; /* ‚úÖ Poprawiony odstƒôp miƒôdzy liniami */
  }

  .subtitle {
    font-size: 1.2rem;
    max-width: 80vw;
    text-align: center; /* ‚úÖ Wyr√≥wnanie do ≈õrodka na ma≈Çych ekranach */
  }
  .buttons {
    flex-direction: column;
    gap: 0.8rem;
  }

  .button {
    font-size: 1rem;
    padding: 0.8rem 1.8rem;
  }
}
