@import '../../../styles/variables';

/* üîπ KONTAINER */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  background: var(--primary-bg);
  color: var(--text-color);
  transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
}

/* üîπ Sekcja Portfolio */
.portfolio {
  background: var(--secondary-bg);
  color: var(--text-color);
  text-align: center;
  padding: 2rem;
  max-width: 1200px;
  border-radius: 15px;
  margin: 100px auto 50px;
  /* ‚úÖ Wiƒôkszy margines u g√≥ry i na dole */
  min-height: 100vh;
  /* ‚úÖ Portfolio zajmuje ca≈ÇƒÖ stronƒô */
  transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
}

/* üîπ Tytu≈Ç sekcji */
.portfolio h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 2rem;
}

/* üîπ Siatka projekt√≥w */
.projectGrid {
  display: flex;
  flex-direction: column;
  /* üîπ Projekty uk≈ÇadajƒÖ siƒô pionowo */
  gap: 40px;
  /* üîπ Dodatkowa przestrze≈Ñ miƒôdzy projektami */
}

/* üîπ Pojedyncza karta projektu */
.projectCard {
  background: var(--primary-bg);
  padding: 2rem;
  border-radius: 15px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  align-items: center;
  gap: 20px;
  width: 100%;
  /* üîπ Pe≈Çna szeroko≈õƒá */
  max-width: 1000px;
  /* üîπ Maksymalna szeroko≈õƒá */
  margin: 0 auto;
  /* üîπ Wycentrowanie */

  h3 {
    margin-top: 1rem;
  }
}

/* üîπ Miniatura projektu */
.previewImage {
  width: 40%;
  /* üîπ Szeroko≈õƒá obrazka */
  max-width: 400px;
  height: auto;
  border-radius: 15px;
  transition: transform 0.3s ease-in-out;
}

.previewImage:hover {
  transform: scale(1.05);
}

/* üîπ Opis projektu */
.projectInfo {
  flex: 1;
  /* üîπ Reszta tre≈õci zajmuje dostƒôpne miejsce */
}

.description {
  font-size: 1rem;
  color: var(--secondary-text);
  margin-top: 1rem;
  margin-bottom: 1rem;
}

/* üîπ Cechy projektu (ikonki + tekst) */
.features {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
  margin: 15px 0;
}

/* üîπ Pojedyncza cecha - Ustawienie r√≥wnej szeroko≈õci */
.feature {
  display: flex;
  align-items: center;
  background: var(--primary-bg);
  padding: 8px 12px;
  border-radius: 15px;
  font-size: 0.9rem;
  box-shadow: 0 2px 5px var(--box-shadow);
  transition: transform 0.2s ease-in-out;

  /* üîπ Sprawia, ≈ºe wszystkie cechy sƒÖ tej samej szeroko≈õci */
  flex: 1 1 180px;
  /* Min 180px, elastyczne skalowanie */
  max-width: 220px;
  /* Maksymalna szeroko≈õƒá */
  text-align: center;
  /* Wy≈õrodkowanie tekstu */

  svg {
    font-size: 1.2rem;
    margin-right: 8px;
    color: var(--primary-color);
  }
}

.feature:hover {
  transform: scale(1.05);
}

/* üîπ Stack technologii */
.techSection {
  margin: 15px auto;
  /* üîπ Wy≈õrodkowanie */
  max-width: 600px;
  /* üîπ Ograniczamy szeroko≈õƒá sekcji */
  background: var(--input-bg);
  padding: 15px 20px;
  border-radius: 15px;
  text-align: center;

  .techTitle {
    font-size: 1.1rem;
    font-weight: bold;
    margin-bottom: 8px;
    color: var(--primary-color);
  }

  .techStack {
    display: flex;
    justify-content: center;
    gap: 8px;
    flex-wrap: wrap;
    max-width: 500px;
    /* üîπ Ograniczamy szeroko≈õƒá listy */
    margin: 0 auto;
    /* üîπ Wy≈õrodkowanie */
  }

  .tech {
    background: var(--primary-color);
    color: white;
    padding: 6px 12px;
    border-radius: 15px;
    font-size: 0.9rem;
    white-space: nowrap;
    /* üîπ Zapobiega rozciƒÖganiu przycisk√≥w */
  }
}

/* üîπ Przyciski akcji */
.actions {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  gap: 10px;
}

/* üîπ Przycisk "Zobacz galeriƒô" */
.viewGalleryButton,
.expandButton {
  background: var(--primary-color);
  color: white;
  font-size: 1rem;
  padding: 0.8rem 1.8rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease-in-out, transform 0.2s;
  border: none;
}

.viewGalleryButton:hover,
.expandButton:hover {
  transform: scale(1.05);
}

/* üîπ Rozwiniƒôty opis projektu */
.expandedContent {
  background: var(--secondary-bg);
  color: var(--text-color);
  padding: 15px;
  margin-top: 10px;
  border-radius: 15px;
  box-shadow: 0 3px 8px var(--box-shadow);
  text-align: left;
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* üîπ Lightbox - pe≈Çnoekranowy podglƒÖd zdjƒôƒá */
.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3000;
  padding: 20px;
}

/* üîπ Kontener zawarto≈õci lightboxa */
.lightboxContent {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* üîπ Ukrycie scrolla strony podczas aktywnego lightboxa */
body.lightbox-open {
  overflow: hidden;
  height: 100vh;
}

/* üîπ Obrazek w lightboxie */
.largePreview {
  max-width: 90%;
  max-height: 80vh;
  border-radius: 15px;
  transition: transform 0.3s ease-in-out;
}

/* üîπ Kontener przycisk√≥w (Poprzednie/Nastƒôpne zdjƒôcie) */
.lightboxControls {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 15px;
}

/* üîπ Przyciski nawigacyjne */
.prevButton,
.nextButton {
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 1.1rem;
  cursor: pointer;
  padding: 0.8rem 1.8rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease-in-out, transform 0.2s;

  &:hover {
    transform: scale(1.05);
  }
}

/* üîπ Przycisk zamkniƒôcia - poprawiona pozycja */
.closeButton {
  position: absolute;
  top: -60px; /* üîπ Umieszczony wy≈ºej nad lightboxem */
  right: 50%;
  transform: translateX(50%); /* üîπ Wy≈õrodkowanie */
  background: var(--color-warning);
  color: white;
  width: auto;
  height: auto;
  padding: 0.8rem 1.8rem;
  border: none;
  border-radius: 25px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background 0.3s ease-in-out, transform 0.2s;
}

/* üîπ Efekt hover */
.closeButton:hover {
  transform: translateX(50%) scale(1.05);
}

/* üîπ Responsywno≈õƒá */
@media (max-width: 1024px) {
  .projectGrid {
    flex-wrap: wrap;
    /* üîπ Dla mniejszych ekran√≥w pozwalamy na ≈Çamanie siƒô */
    justify-content: center;
  }

  .closeButton {
    top: -50px; /* üîπ Lekko przesuniƒôty bli≈ºej */
    font-size: 1rem;
    padding: 10px 15px;
  }
}

@media (max-width: 768px) {
  .portfolio h2 {
    font-size: 2rem;
  }

  .projectGrid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }

  .projectCard {
    padding: 1.5rem;
  }

  .actions {
    flex-direction: column;
  }

  .lightbox {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
  }

  .lightboxContent {
    width: 100%;
    max-width: 95%;
    justify-content: center;
    align-items: center;
    padding: 0;
  }

  .largePreview {
    max-width: 90%;
    max-height: 60vh;
  }

  .lightboxControls {
    flex-direction: row;
    width: 100%;
    justify-content: center;
    margin-top: 15px;
  }

  .prevButton,
  .nextButton {
    font-size: 1rem;
    width: auto;
  }

  .closeButton {
    top: -50px;
    right: 20%;
    transform: translateX(50%);
    font-size: 1rem;
    //padding: 8px 12px;
  }
}