@import '../../../styles/variables';

/* üîπ STOPKA */
.footer {
  background: var(--footer-bg); /* ‚úÖ Dynamiczne t≈Ço */
  color: var(--footer-text); /* ‚úÖ Dynamiczny kolor tekstu */
  padding: 4rem 2rem;
  text-align: center;
  border-top: 2px solid var(--border-color);
  animation: fadeIn 1.5s ease-in-out;
  transition: background-color 0.3s ease-in-out, transform 0.2s;
}

/* üîπ FADE-IN ANIMATION */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.fadeIn {
  animation: fadeIn 1s ease-in-out;
}

/* üîπ SEKCJE STOPKI */
.footerContainer {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  text-align: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  flex-wrap: wrap;
}

/* üîπ SEKCJA */
.section {
  flex: 1;
  min-width: 280px;
  max-width: 350px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
  background-color: var(--footer-bg);
  height: 100%;
}

/* üîπ NAG≈Å√ìWEK SEKCJI */
.section h3 {
  font-family: $heading-font;
  font-weight: 600;
  font-size: 1.3rem;
  color: var(--text-color);
  margin-bottom: 0.5rem;
}

/* üîπ LINKI */
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul li {
  margin: 0.4rem 0;
}

ul li a {
  text-decoration: none;
  color: var(--primary-color);
  font-size: 1.1rem;
  transition: color 0.3s ease-in-out, transform 0.2s;

  &:hover {
    color: var(--primary-color-hover); /* ‚úÖ Zmienna CSS zamiast darken() */
  }
}

/* üîπ NEWSLETTER */
.newsletter {
  width: 100%;
  text-align: center;
  padding: 2rem 0;
  max-width: 1000px;
  margin: 2rem auto;
}

.newsletterForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  max-width: 400px;
  margin: 0 auto;
}

.newsletterForm input {
  padding: 12px;
  border: 1px solid var(--border-color);
  border-radius: 15px;
  font-size: 1rem;
  width: 100%;
  width: 400px;
  text-align: center;
  font-family: $body-font;
  background: var(--primary-bg);
  color: var(--text-color);
}

/* üîπ PRZYCISK NEWSLETTERA */
.newsletterForm button {
  padding: 0.8rem 1.8rem;
  border: 2px solid var(--primary-color);
  background: var(--button-footer-bg); /* ‚úÖ Dynamiczny kolor */
  color: var(--button-text);
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out, transform 0.2s;

  &:hover {
    transform: scale(1.05);
  }
}

/* üîπ SOCIAL MEDIA */
.socialSection {
  width: 100%;
}

.socialIcons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 1rem;
}

.socialIcons a {
  font-size: 2rem;
  color: var(--secondary-text);
  transition: background-color 0.3s ease-in-out, transform 0.2s;

  &:hover {
    transform: scale(1.05);
  }
}

/* üîπ DYNAMICZNE INFORMACJE */
.extraInfo {
  margin-top: 2rem;
  font-size: 1rem;
  color: var(--secondary-text);
}

/* üîπ COPYRIGHT */
.copyright {
  margin-top: 2rem;
  font-size: 0.9rem;
  color: var(--secondary-text);
}

/* üîπ PRZYCISK DO G√ìRY */
.scrollTop {
  position: fixed;
  bottom: 25px;
  right: 25px;
  background: var(--button-footer-bg); /* ‚úÖ Dynamiczny kolor */
  color: white;
  width: 55px; /* ‚úÖ Gwarantujemy okrƒÖg≈Çy kszta≈Çt */
  height: 55px; /* ‚úÖ Powinno byƒá r√≥wne szeroko≈õci */
  border: none;
  display: flex;
  border-radius: 25px; /* ‚úÖ Zapewnia pe≈ÇnƒÖ okrƒÖg≈Ço≈õƒá */
  font-size: 1.5rem;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease-in-out, transform 0.2s;
  z-index: 2500;
  cursor: pointer;

  &:hover {
    transform: scale(1.05);
  }
}


/* üîπ RESPONSYWNO≈öƒÜ */
@media (max-width: 768px) {
  .footerContainer {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .section {
    text-align: center;
    max-width: 400px;
  }

  .newsletter {
    padding: 1.5rem;
  }

  .newsletterForm {
    width: 100%;
  }

  .newsletterForm input {
    width: 300px;
  }

  .copyright p {
    margin-bottom: 20px;
  }
}
