@import '../../../styles/variables';

/* SEKCJA O MNIE */
.about {
  padding: 6rem 2rem 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  background: var(--secondary-bg);
  border-radius: 15px;
  max-width: 1200px;
  margin: 0 auto;
  gap: 4rem;
  position: relative;
  overflow: hidden;
  margin-bottom: 3rem;
  transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
}

/* SEKCJA TEKSTOWA */
.textSection {
  max-width: 55%;
  animation: fadeIn 1s ease-in-out;
}

.about h2 {
  font-size: 3rem;
  font-weight: 700;
  color: var(--primary-color); /* ‚úÖ Zielony nag≈Ç√≥wek */
  margin-bottom: 1rem;
}

.about h3 {
  font-size: 1.7rem;
  font-weight: 600;
  color: var(--primary-color); /* ‚úÖ Zielony tytu≈Ç */
  margin-top: 1.5rem;
}

.about p {
  font-size: 1.2rem;
  color: var(--secondary-text);
  line-height: 1.7;
  margin-bottom: 1rem;
}

/* PRZYCISK CTA */
.button {
  display: inline-block;
  background: var(--button-bg);
  color: var(--button-text);
  padding: 0.8rem 1.8rem;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.3s ease-in-out, transform 0.2s;
  font-size: 1.2rem;
  margin-top: 1.5rem;
}

.button:hover {
  transform: scale(1.05);
}

/* SEKCJA ZDJƒòCIA */
.imageSection {
  max-width: 40%;
  animation: fadeIn 1s ease-in-out;
}

.imageSection img {
  width: 100%;
  max-width: 350px;
  border-radius: 15px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease-in-out;
}

.imageSection img:hover {
  transform: scale(1.05);
}

/* FADE-IN */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* RESPONSYWNO≈öƒÜ */
@media (max-width: 768px) {
  .about {
    flex-direction: column;
    text-align: center;
    padding: 4rem 2rem;
  }

  .textSection {
    max-width: 100%;
  }

  .imageSection {
    max-width: 100%;
    margin-top: 2rem;
  }

  .imageSection img {
    max-width: 250px;
  }

  .about h2 {
    font-size: 2.4rem;
  }

  .about p {
    font-size: 1.1rem;
  }

  .button {
    font-size: 1rem;
    padding: 0.8rem 1.8rem;
  }
}
